import React, { useContext } from "react";
import firebase from "../../../shared/firebase";
import styled from "styled-components";
import ModalContext from "../../../context/ModalContext";

const ReportWrap = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: white;
  width: 30rem;
  padding: 1rem;
  border-radius: 10px;
`;

const ReportContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 0.5rem;
`;

const ReportBody = styled.div`
  display: flex;
  justify-content: center;
  height: 10rem;
  padding-bottom: 1rem;
`;

const ReportTextBox = styled.textarea`
  height: 100%;
  width: 100%;
  border: solid 1px black;
  border-radius: 5px;
  padding: 0.6em;
  margin: 0 auto;
  resize: none;
`;
const ButtonWrap = styled.div`
  display: flex;
  justify-content: flex-end;
`;
const Button = styled.button`
  width: fit-content;
  padding: 0.3rem 1rem;
  font-size: 1.1rem;
  border-radius: 10px;
  margin-left: 3px;
  color: white;
`;
const ReportModal = props => {
  const [, setModal] = useContext(ModalContext);
  const handleClickCheckBtn = e => {
    setModal(state => ({ ...state, isOpen: false }));
  };

  function ReportSubmit(e) {
    e.preventDefault();
    if (e.target.type.value === "") {
      return alert(" .");
    }
    if (e.target.reportText.value === "") {
      alert(" .");
    } else {
      if (e.target.type.value === "user") {
        firebase.writeUserReport(props.userId, e.target.reportText.value); //, 
      } else {
        firebase.writeProductReport(props.productId, e.target.reportText.value); //, 
      }
      e.target.reportText.value = "";
      alert(" .");
      handleClickCheckBtn();
    }
  }

  return (
    <ReportWrap>
      <form method="POST" onSubmit={ReportSubmit}>
        <h2></h2>
        <ReportContainer>
          <div>
            <select name="type" defaultValue={""}>
              <option value="" disabled hidden>
                  
              </option>
              <option value="user"> </option>
              <option value="product"> </option>
            </select>
          </div>
          <ReportBody>
            <ReportTextBox name="reportText" />
          </ReportBody>
        </ReportContainer>

        <ButtonWrap>
          <Button className={"danger"} onClick={handleClickCheckBtn}>
            
          </Button>
          <Button className={"success"}></Button>
        </ButtonWrap>
      </form>
    </ReportWrap>
  );
};

export default ReportModal;
